.main-menu {
    padding: 10px 20px;
    display: flex;
    background-color: #243647;
    align-items: center;
    height: 60px;
    color: gold;
    /*font-family: "Courier New", Courie, monospace;*/
}

.main-menu li {
    list-style-type: none;
    margin-right: 20px;
}

.main-menu li a.active {
    -webkit-animation: neon5 1.5s ease-in-out infinite alternate;
    -moz-animation: neon5 1.5s ease-in-out infinite alternate;
    animation: neon5 1.5s ease-in-out infinite alternate;
}

.main-menu li.rihgt-top {
    flex-grow: 2;
    display: flex;
    justify-content: flex-end;
    border: none;
    margin: 0;
}

.main-menu li.rihgt-top a {
    border: none
}

/*NEON*/


a {
    text-decoration: none;
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    transition: all 0.5s;
}

.logo {
    color: #fff;
    padding-right: 50px;
    font-size: 35px;
    font-weight: 400;
    font-family: 'Monoton', cursive;
    -webkit-animation: neon1 1.5s ease-in-out infinite alternate;
    -moz-animation: neon1 1.5s ease-in-out infinite alternate;
    animation: neon1 .5s ease-in-out infinite alternate;
}

.logo:hover {
    color: gold;
    -webkit-animation: none;
    -moz-animation: none;
    animation: none;
}

p:nth-child(2) a {
    font-size: 1.5em;
    color: #228DFF;
    font-family: Iceland;
}

p:nth-child(2) a:hover {
    -webkit-animation: neon2 1.5s ease-in-out infinite alternate;
    -moz-animation: neon2 1.5s ease-in-out infinite alternate;
    animation: neon2 1.5s ease-in-out infinite alternate;
}

p:nth-child(3) a {
    color: #FFDD1B;
    font-family: Pacifico;
}

p:nth-child(3) a:hover {
    -webkit-animation: neon3 1.5s ease-in-out infinite alternate;
    -moz-animation: neon3 1.5s ease-in-out infinite alternate;
    animation: neon3 1.5s ease-in-out infinite alternate;
}

p:nth-child(4) a {
    color: #B6FF00;
    font-family: "Press Start 2P";
    font-size: 0.8em;
}

p:nth-child(4) a:hover {
    -webkit-animation: neon4 1.5s ease-in-out infinite alternate;
    -moz-animation: neon4 1.5s ease-in-out infinite alternate;
    animation: neon4 1.5s ease-in-out infinite alternate;
}

.neon {
    font-size: 20px;
    color: gold;
    font-weight: bold;
    font-family: Audiowide;
}

.neon:hover {
    -webkit-animation: neon5 1.5s ease-in-out infinite alternate;
    -moz-animation: neon5 1.5s ease-in-out infinite alternate;
    animation: neon5 1.5s ease-in-out infinite alternate;
}

p:nth-child(6) a {
    color: #BA01FF;
    font-family: Vampiro One;
}

p:nth-child(6) a:hover {
    -webkit-animation: neon6 1.5s ease-in-out infinite alternate;
    -moz-animation: neon6 1.5s ease-in-out infinite alternate;
    animation: neon6 1.5s ease-in-out infinite alternate;
}

p a:hover {
    color: #ffffff;
}

