.moviesTable {
    background-color: #243647;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 15px;
}

.moviesTable table {
    border-collapse: collapse;
    width: 100%;
}

.moviesTable table a:hover{
    color: #008e00;
    cursor: pointer;
}

.moviesTable table thead {
    font-family: "Courier New", Courie, monospace;
    font-weight: bold;
    color: #b89900;
    font-size: 19px;
}

.moviesTable table thead td {
    border: none;
}

.moviesTable table td {
    padding: 15px 15px 15px 0;
    border-top: 1px solid #515151;
}

.moviesTable .total {
    opacity: .5;
}
.smallImg{
    height: 80px;
}
.content .tip{
    margin: 0 0 25px 0;
}
