.modalWindow {
    position: fixed;
    z-index: 2000;
    background: #243647;
    width: 50vw;
    border-radius: 10px;
    height: 60vh;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.9);
    display: flex;
    align-items: stretch;
    justify-content: stretch;
}

.poster {
    width: 35%;
    height: 100%;
    flex-shrink: 0;
    border-radius: 10px 0 0 10px;

}

.modalWindow .movieInfo {
    padding: 25px;
}

.movieYear {
    opacity: 0.6;
}

.imdbRating {
    font-size: 25px;
    color: goldenrod;
    padding-bottom: 35px;
}

.modalWindow .movieInfo p span {
    color: goldenrod;
}

.modalWindow .stars {
    margin-top: 20px;
}

.modalWindow .stars span {
    letter-spacing: 10px;
    font-size: 30px;
}
.rateThis{
    margin-top: 25px;
    color: goldenrod;
}
.rateThis .star {
    font-size: 27px;
    cursor: pointer;
    opacity: .4;
    transition: 0.3s;
    padding-right: 7px;
}

.rateThis .star.active {
    opacity: 1;
    transition: 0.3s;

}

.modalWindow .hint{
    opacity: 0.5;
    margin-top: 30px;
}
